*{
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Montserrat', sans-serif;
    background: rgb(0, 0, 0);
    color: white;
}
a{
    color: white;
    text-decoration: none;
}
.desktop{
    display: none;
}
.button{
    padding: 3vw 5vw 3vw 5vw;
    font-size: 4.5vw;
    border-radius: 50px;
    background-color: transparent;
    border: 0.6vw solid white;
    color: white;
    font-weight: 700;
    cursor: pointer;
}
.underlined{
    position: relative;
}
.underlined::before{
    content: "";
    position: absolute;
    top: 90%;
    width: 100%;
    left: 0;
    height: 5px;
    border-radius: 2px;
    background: linear-gradient(111.3deg, rgb(0, 0, 255) 9.6%, rgb(0, 150, 255) 93.6%);
}
.appearing{opacity: 0; transition: all 1.5s ease; }
@keyframes animate{
    0%{
      background: linear-gradient(to top,  rgb(0, 150, 255), rgb(0, 0, 255));
    }
    8%{
      background: linear-gradient(to top, rgb(0, 125, 255), rgb(0, 25, 255));
   }
   17%{
      background: linear-gradient(to top, rgb(0, 100, 255), rgb(0, 50, 255));
   }
   25%{
      background: linear-gradient(to top, rgb(0, 75, 255), rgb(0, 75, 255));
  }
  33%{
      background: linear-gradient(to top, rgb(0, 50, 255), rgb(0, 100, 255));
  }
  40%{
      background: linear-gradient(to top, rgb(0, 25, 255), rgb(0, 125, 255));
  }
  47%{
      background: linear-gradient(to top, rgb(0, 0, 255), rgb(0, 150, 255));
  }
  55%{
      background: linear-gradient(to top, rgb(0, 25, 255), rgb(0, 125, 255));
  }
  63%{
      background: linear-gradient(to top, rgb(0, 50, 255), rgb(0, 100, 255));
  }
  70%{
      background: linear-gradient(to top, rgb(0, 75, 255), rgb(0, 75, 255));
  }
  78%{
      background: linear-gradient(to top, rgb(0, 100, 255), rgb(0, 50, 255));
  }
  86%{
      background: linear-gradient(to top, rgb(0, 125, 255), rgb(0, 25, 255));
  }
  100%{
      background: linear-gradient(to top, rgb(0, 150, 255), rgb(0, 0, 255));
  }
}
.page{
    position: relative;
}
.main-header{
    font-size: 8vw;
    font-weight: 800;
    margin: 0vw 0 0 6vw;
    max-width: 90vw;
}
.next-header{
    margin: 25vw 0 10vw 6vw;
    font-size: 10vw;
    max-width: 92vw;
}
#membership-header{
    margin-top: 25vw;
}
.about{
    font-size: 4.5vw;
    margin: 7vw 0 0 6vw;
    max-width: 90vw;
    color: rgba(255, 255, 255, 0.774);
}
.join-btn{
    position: absolute;
    margin: 10vw 0 0 6vw;
    transition: all 0.5s ease;
}
.join-btn:hover{
    color: black;
    background-color: white;
}
.main-image{
    width: 100%;
    height: 70vw;
    margin-top: 35vw;
    background-image: url("../images/GymExterior.png");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-box-shadow: inset 0 0 10px 10px #000;    
    -moz-box-shadow: inset 0 0 10px 10px #000;
    box-shadow: inset 0 0 10px 10px #000;
}
#team-header{
    margin-bottom: 0;
}
.team-container{
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 20vw;
}
.faq-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.end-btn-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20vw;
}
.end-dc-btn{
    width: 75vw;
    height: 52vw;
    border-radius: 30px;
    border: none;
    background: linear-gradient(14deg, rgba(0,0,255,1) 35%, rgba(17,245,253,1) 100%);
    text-align: center;
    padding-top: 8vw;
}
.end-dc-text{
    width: 100%;
    font-size: 8vw;
    font-weight: 700;
    color: white;
}
.discord-img{
    background-image: url("../images/discord.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    width: 20vw;
    height: 15vw;
    margin: 0 auto;
    margin-top: 5vw;
}
.mint-btn{
    background: linear-gradient(10deg, rgba(0,0,255,1) 35%, rgba(17,245,253,1) 100%);
    border: none !important;
    position: absolute;
    margin: 12vw 0 0 6vw;
    width: 40vw;
    height: 8vw;
    text-align: center;
    font-size: 4vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ga-conrner-logo{
    display: none;
}
